@import "mixins";
@import "paragraphs";
@import "tooltip";

.node-form textarea {
  max-width: 750px;
}

.node {
  &__content {
    @include va-basic-styling;
  }
}

/* Node view from page--node.html.twig */

.node-columns {
  display: flex;
  flex-flow: row wrap;

  /* Single column */
  > * {
    flex: 1 100%;
  }

  /* Asides form two columns on small tablets. */
  @media all and (min-width: 600px) {
    aside {
      flex: 1 0 0;
    }
  }

  /* Three column layout on larger devices. */
  @media all and (min-width: 800px) {
    main {
      flex: 3 0;
    }

    .layout-sidebar-first {
      order: 1;
    }

    main {
      order: 2;
      padding: 0 2em;
    }

    .layout-sidebar-second {
      order: 3;
    }
  }
}

/* Right sidebar on node view pages. */

.view-id-right_sidebar_latest_revision .views-row {
  padding-bottom: 1em;
}

/* Admin menus with route:<nolink> links should look OK */
.toolbar-tray span.toolbar-icon {
  padding: 1em 1.333em;
  display: block;
  color: #888;
  cursor: pointer;
}

#block-vagovadmin-content {
  .layout-region-node-main,
  .layout-region-node-footer {
    display: grid;
    overflow: auto;
  }
}

.node--unpublished {
  background-color: #fcf6e4;
}

.page-node-type-page {
  .node--view-mode-full {
    font-size: 1rem;
    .node__content > .field {
      margin-bottom: 1.5rem;
    }

    h1 {
      line-height: 2.5rem;
      margin-bottom: 2rem;

      .field--name-title {
        font-family: "Bitter", "Georgia", "Cambria", "Times New Roman", "Times", serif;
        font-size: 2rem;
      }
    }

    .meta-content-wrapper {
      .meta-content {
        background-color: #eff6f6;

        tbody {
          tr {
            border: none;

            &:hover {
              background-color: inherit;
            }

            td {
              padding: 0;
              font-size: 0.8rem;
              margin-right: 0.8rem;
            }

            &.field--label-inline {
              .field__label:after {
                content: '';
              }
            }
          }
        }
      }

      .seo-alert .field__label {
        color: #cc0000;
      }
    }

    fieldset:not(.fieldgroup) {
      margin: 9px 0 10px 0;
      padding: 10px 18px 0;
      background-color: inherit;

      > legend {
        position: inherit;
        display: none;
      }
    }

    .field__item {
      margin: 0.2rem 0.2rem 0.5rem 0.2rem;
    }

    .field--label-above {
      .field__label {
        font-size: 1rem;
        color: #7b7b7b;
        margin: 0;
        padding: .2rem 0 0 0;
        font-weight: normal;
      }
    }

    .field--name-field-intro-text .field__item {
      font-family: Bitter, Georgia, Cambria, Times New Roman, Times, serif;
      font-size: 1.25rem;
      font-weight: 400;
      max-width: 77rem;
      line-height: 2rem;
      margin: 0;
    }
    .field--name-field-featured-content {
      .field__items {
        background-color: #e1f3f8;
      }
    }
  }
  .field--name-field-related-links .field__item  {
    background: #f1f1f1;
  }


  body .version-1 {
    font-size: 1rem;
  }
}
